import TomTomAttributionControl from '../display/attribution/tomTomAttributionControl';
import { copyrightsCaption } from 'Services/services/copyrights/copyrightsCaption';

export default async (mapsKey, map) => {
    const caption = await copyrightsCaption({
        key: mapsKey
    }).go();
    const markup = `<a class="tomtomAttribution">${caption.copyrightsCaption}</a>`;
    const attribution = new TomTomAttributionControl(mapsKey);
    map.addControl(attribution);
    map._attributionControl = attribution;
    attribution.addAttribution(markup);
    //TODO: Refactor this event and create a unified way of firing events
    map.__om.fire('ATTRIBUTION_LOAD_END', {data: markup});
};



